/**课程接口 */
import request from "@/utils/request"
import qs from "qs"

//获取课程页数据
export const getAllCourseData = (params) => {
  return request.post(`/school/course/courses`, qs.stringify(params))
}

//获取课程详情
export const getCourseDetail = (courseId, params) => {
  return request.post(`/school/course/detail/${courseId}`, qs.stringify(params))
}

//获取播放页详情
export const getCourseLiveDetail = (id, params) => {
  return request.post(`/school/course/play/${id}`, qs.stringify(params))
}

//播放事件上报
export const reportCourseLived =  (params) => {
  return request.post(`/school/course/report/${params.courseId}/${params.liveId}`, qs.stringify(params.options))
}

//收藏
export const collectCourse = (courseId, favorite) => {
  return request.post(`/school/favorite/favorite/${courseId}/${favorite}`)
}

//获取课程额外的信息
export const getCourseMsg = (courseId) => {
  return request.post(`/school/course/info/${courseId}`)
}

/**分享*/
export const getShareConfig = (params) => {
  return request.post(`/service/app/mp/makeJsMap`, qs.stringify(params), {
    custom: { // 自定义的额外参数
      isAis: true
    }
  })
}

export const getShareWXConfig = (params) => {
  return request.post(`/service/app/mp/makeJsMap`, qs.stringify(params))
}



//免费课程报名
export const applyFreeCourse = (courseId) => {
  return request.post(`/school/student/apply/free/${courseId}`)
}

//获取确认订单页面详情
export const getCourseOrder = (courseId) => {
  return request.post(`/school/student/trade/index/${courseId}`)
}

//获取推荐课程
export const getHomeTops = () => {
  return request.post(`/school/home/tops`)
}

//下单购买
export const buyCourse = (params) => {
  return request.post(`/school/student/trade/buy/${params.courseId}`, qs.stringify(params.options))
}

//获取推广佣金
export const getCourseCarryFee = (courseId) => {
  return request.post(`/school/course/promotion/${courseId}/carryFee`)
}


//获取课程推广链接
export const getCourseLink = (courseId) => {
  return request.get(`/school/course/promotion/${courseId}/link`)
}

//获取课程讨论区一级列表
export const getForumFirstComment = (parmas) => {
  let { courseId, ...rest } = parmas
  return request.post(`/school/course/comment/list/${courseId}`, qs.stringify(rest))
}

//获取课程讨论区二级列表
export const getForumSecondComment = (parmas) => {
  let { courseId, parentId } = parmas
  return request.post(`/school/course/comment/subList/${courseId}/${parentId}`)
}

//提交讨论区评论
export const submitForumComment = (parmas) => {
  return request.post(`/school/course/comment/submit`, qs.stringify(parmas))
}


//课程发表评论
export const sendComment = (params) => {
  let { courseId, ...rest  } = params
  return request.post(`/school/course/score/submit/${courseId}`, qs.stringify(rest))
}

//获取课程评论
export const getCommentList = (params) => {
  let { courseId, ...rest  } = params
  return request.post(`/school/course/score/list/${courseId}`, qs.stringify(rest))
}

//购买成功页，问答提交
export const submitProblem = (params) => {
  return request.post(`/school/student/apply/problem/save`, qs.stringify(params))
}

//获取播放页推荐信息
export const getLoadPushMsg = (params) =>{
  const { courseId, liveId } = params
  return request.post(`/school/course/loadPush/${courseId}/${liveId}`)
}

//获取最新购买课程用户信息
export const getCourseLastBuy = (courseId) => {
  return request.post(`/school/course/score/latestBuy/${courseId}`)
}
//课件下载
export const courseDownlaod = (params) => {
  return request.post(`/school/course/down/document/${params.courseId}/${params.liveId}`)
}


//课程分享回调
export const courseShareCallBack = (courseId) => {
  return request.post(`/school/course/callback/${courseId}`)
}

//获取课程客服二维码
export const getCourseQrCode = (params) => {
  return request.post(`/school/student/trade/complete`, qs.stringify(params))
}

//打卡-课程打卡记录列表
export const getClockRecordList = (courseId, params) =>{
  return request.post(`/school/student/clock/record/list/${courseId}`, qs.stringify(params))
}

// 打卡课程详情
export const getCourseClockInfo = (courseId) => {
  return request.post(`/school/student/clock/main/course/info/${courseId}`)
}

//打卡-打卡评价列表
export const getClockComment = (recordId, params) => {
  return request.post(`/school/student/clock/record/comment/list/${recordId}`, qs.stringify(params))
}

//课程回答历史
export const getProblemHistory = () => {
  return request.post(`/school/student/apply/problem/history`)
}

//集call活动首页
export const getCallIndex = (params) => {
  return request.post(`/school/activity/call/index/${params}`)
}

//集call活动参加
export const joinCall = (activityId) => {
  return request.post(`/school/activity/call/join/${activityId}`)
}

//集call活动海报
export const getCallPoster = ({recordId, index}) => {
  return request.post(`/school/activity/call/poster/${recordId}/${index}`)
}

//集call活动打call
export const applyCall = (recordId) => {
  return request.post(`/school/activity/call/help/${recordId}`)
}

//集call活动手动解锁
export const lockCall = (recordId) => {
  return request.post(`/school/activity/call/unlock/${recordId}`)
}