import config from "@/config/common.config"
import { Swiper, SwiperSlide } from 'vue-awesome-swiper'
import 'swiper/swiper-bundle.css'
import { downLoadLink  } from 'ais-common-utils'
import { getCallIndex } from '@/api/course'
export default {
  components: {
    Swiper,
    SwiperSlide
  },
  data() {
    return {
      id:this.$route.params.id,
      config,
      swiperOptions:{
        slidesPerView:1.2,
        loop: true,
        pagination: {
          el:'.swiper-pagination',
          clickable: true
        },
      },
      show:false,
      activityId:this.$route.query.activityId,
      data:{}
    }
  },
  computed:{
    copyContent() {
      const title = `${window.location.origin}/courseCall/${this.activityId}?recordId=${this.id}`
      return this.data && this.data.call && this.data.call.share ? `${this.data.call.share} ${title}` : title
    }
  },
  mounted() {
    this.getListHandle()
  },
  methods:{
    async getListHandle() {
      const { data } = await getCallIndex(this.activityId)
      if(data.code === 0) {
        this.data = data.data
      }
    },
    downLoad(){
      downLoadLink(`${config.POST.school_h5}/school/activity/call/poster/${this.id}/${this.$refs.mySwiper.$swiper.realIndex+1}`, '集call免费学.png')
    },
    onCopySuccess() {
      this.$toast.success("复制成功")
      this.show = false
    }
  },
  head(){
    return{
      title:'集call免费学 --艾思云课堂'
    }
  }
}
